@font-face {
  font-family: 'IRANYekanWebRegular';
  src: url('/src/assets/fonts/IRANYekanWebRegularNum.woff'); /* IE9 Compat Modes */
  src: url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('embedded-opentype'), /* IE6-IE8 */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('woff2'), /* Super Modern Browsers */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('woff'), /* Pretty Modern Browsers */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff')  format('truetype'), /* Safari, Android, iOS */
       url('/src/assets/fonts/IRANYekanWebRegularNum.woff') format('svg'); /* Legacy iOS */
}

* {
  font-family: 'IRANYekanWebRegular' !important;
  }

body{
  background-color:#F7F8F9 !important;
}
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #25233c; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.4); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.highcharts-container {
  margin: auto;
}
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.dashExport {
  stroke-dasharray: 3;
  stroke: #2ABB7F;
  stroke-dashoffset: 100;
   animation: dashExport 10s linear infinite;
}
.dashImport {
  stroke-dasharray: 3;
  stroke: #EF5C48;
  stroke-dashoffset: 100;
   animation: dashImport 10s linear infinite;
}
@keyframes dashExport {
  from {
      stroke-dashoffset: 100;
  }

  to {
      stroke-dashoffset: 20;
  }
}
@keyframes dashImport {
  from {
      stroke-dashoffset: 20;
  }

  to {
      stroke-dashoffset: 100;
  }
}
